/*--------------------------------------------------------------------------------*/
/*                                  starter                                    */
/*--------------------------------------------------------------------------------*/
import FirstDashboard from '../views/starter/starter.jsx';
/*--------------------------------------------------------------------------------*/
/*                           Ui-components Dropdown                               */
/*--------------------------------------------------------------------------------*/
import Dayooks from '../views/ui-components/dayook.jsx';
import Yasang from '../views/ui-components/Yasang.js';
import Plant from '../views/ui-components/Plant.js';
import Animals from '../views/ui-components/Animals';




//import icon1 from './icon1.png';
import icon1 from '../assets/images/icon/icon1.png';
import icon2 from '../assets/images/icon/icon2.png';
import icon0 from '../assets/images/icon/icon0.png';
import animal from '../assets/images/icon/icon3.png';
import d7 from '../assets/images/icon/d7.png';

var ThemeRoutes = [
  {
    path: '/starter/starter',
    name: ' 소개',
    icon: icon0, 
    component: FirstDashboard
  },
  {
    path: '/ui-components/dayook1',
    name: ' 다육이',
    icon: d7,
    component: Dayooks
  },
  {
    path: '/ui-components/Yasang',
    name: ' 야생화',
    icon: icon2,
    component: Yasang
  },
  {
    path: '/ui-components/Plant',
    name: ' 화분',
    icon: icon1,
    component: Plant
  },
  {
    path: '/ui-components/Animals',
    name: '농장 지킴이',
    icon: animal,
    component: Animals
  },
  {
    path: '/',
    pathTo: '/starter/starter',
    name: 'Dashboard',
    redirect: true
  },
];

export default ThemeRoutes;
