import React from 'react';
import {
    Row,
    Col
} from 'reactstrap';
import { SalesSummary, Projects, Feeds, SocialCards } from 'components/dashboard-components';

//import Carousel from '../ui-components/carousel.jsx';
import Carousel from '../ui-components/Carousel.js';
import {NaverMap, Marker} from 'react-naver-maps';

const Starter = () => {


function NaverMapAPI(){
    return(
        <NaverMap 
        mapDivId={'maps-getting-started-uncontrolled'}
        style={{width: '50%', height: '50vh'}}
        defaultCenter={{lat: 37.884234, lng: 127.762473}}
        defaultZoom={16}
        >
        <Marker
                  displayName={"d"}
                  icon={"화인 꽃 농원"}
                  key={1}
                  position={{ lat: 37.884234 , lng: 127.762473 }}
                  animation={1}
/*                   onClick={() => {
                    alert('hello');
                  }} */
                />
        </NaverMap>
    );
}



    return (
<>
          <div>
                <Row>
                    <Col sm={6} lg={8}>
                        <Carousel />
                    </Col>
                    <Col sm={6} lg={4}>
                        <Feeds />
                    </Col>
                </Row>
            </div>
        </>
    );
}

export default Starter;